@import 'Colors.css';

.JumbotronContainer {
    max-height: 700px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.JumbotronContainer > video {
    width: 100%;
    min-width: 900px;   
}

.VideoOverlayContainer {
    background-color: rgba(1, 1, 1, 0.1);
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: var(--white);
}

.ShopButton {
    border: 1px solid var(--white);
    padding: 4px 15px;
    border-radius: 5px;
    cursor: pointer;
}

.ShopButton:hover {
    background-color: var(--white);
    color: var(--green);
}

@media (max-width: 1500px) {
    .JumbotronContainer {
        max-height: 550px;
    }
}

@media (max-width: 1000px) {
    .JumbotronContainer {
        max-height: 350px;
    }
}