.ProductsContainer {
    max-width: 1000px;
    display: inline-flex;
    flex-wrap: wrap;
    justify-content: center;
    /* margin   : 40px; */
}

.ProductThumbnail {
    width: 26%;
    margin: 3.5%;
}

.ProductImage {
    height: auto;
    width: auto;
    max-height: 100%;
    max-width: 100%;    
}


@media (max-width: 700px) {
    .ProductThumbnail {
        width: 90%;
        margin: 5%;    
    }
}