@import 'Colors.css';

.ProductContainer {
    justify-content: center;
    display: flex;
    padding: 40px;
}


.ThumbnailsContainer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.ThumbnailImageContainer {
    width: 75px;
    height: 75px;
    margin: 10px;
    border: 1px solid lightgrey;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.Selected {
    border: 3px solid red;
}

.SelectedProductImage {
    text-align: center;
}

.SelectedProductImage > img {
    width: 80%;
}

.QtyAdjustmentContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
}

.QtyLabel > span {
    color: var(--red);
}


.ProductDescription {
    flex-grow: 1;
    text-align: left;
}

.ProductImagesContainer {
    max-width: 600px;
    flex-grow: 1;
}

.ProductInfoContainer {
    flex-grow: 1;
    max-width: 600px;
    display: flex;
    flex-direction: column;
}


@media (max-width: 1000px) {
    .ProductContainer {
        flex-direction: column;
        align-items: center;
    }
}